@import "_variables.scss";

@media only screen and (min-width: 576px){
	.container {
	    max-width: calc(100% - 20px) !important;
	}
}

@media only screen and (min-width: 1160px){
	.container {
	    max-width: 1160px !important;
	}
}


@media only screen and (max-width: 767px){


	body{
		background-position: 50% -2%, 50% 9%, 50% 28%, 50% 55%, 50% 70%, center center;
    	background-size: 190%, 210%, 200%, 200%, 200%, auto;
	}


	.search-container__modal{
		background-image: url('../images/search-modal.png');
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		position: fixed;
		z-index: 103;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding: 20px;
		transform: translateY(-100vh);
		opacity: 0;
		display: block;
		transition: 0.5s;

		&.active{
			transform: translateY(0);
			opacity: 1;
		}

		.container-search{
			padding-right:0px;

			.search-result{
				display: block;
				position: relative;
				left: 0;
				right: 0;
				width: 100%;
				background-image: none;
				box-shadow: none;
				top: 0;
			}

			form{
				display: block;
				float: left;
				width: 100%;
			}

			input{
				width: 100%;
				border: 0px;
				border-radius: 3px;
				background-color: rgba(#C4C4C4, 0.1);
				padding-right: 10px;
				padding-left: 40px;
				height: 25px;
			    line-height: 25px;
			    font-size: 15px;
			}

			button{
				left: 0;
				right: auto;
				opacity: 0.5;
			    top: 28px;

				i{
					font-size: 19px;
				}

				&:hover{
					opacity: 1;
				}
			}
		}

		.close-search{
			position: absolute;
			top: 5px;
			right: 9px;
			display: block;
			width: 20px;
			height: 20px;
			z-index: 10;
			transition: 0.3s;
			opacity: 1;

			&:hover{
				&:before,
				&:after{
					background-color: rgba($color_yellow_black, 1);
				}
			}

			&:before{
				position: absolute;
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				top: 9px;
				left: 0;
				border-radius: 2px;
				background-color: rgba($color_yellow, 1);
				transform: rotate(45deg);
				transition: 0.3s;
			}

			&:after{
				position: absolute;
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				bottom: 9px;
				left: 0;
				border-radius: 2px;
				background-color: rgba($color_yellow, 1);
				transform: rotate(-45deg);
				transition: 0.3s;
			}
		}
	}

	.basket__show-button{
		opacity: 1;

		.icon-icon_cart{
			color: rgba(230, 177, 47, 1);
		}

		.icon-icon_arrow_cart{
			color: rgba(250, 198, 68, 1);
		}
	}

	.section_basket{
		&.active{
			.basket__show-button{
				opacity: 0;
			}
		}
	}

	.button__go-up{
		right: 20px;
		bottom: 20px;
		background-image: url('../images/bg_games.jpg');
    	background-size: cover;
    	color: $full_black;

    	&:hover{
    		color: $full_black;
    	}
	}

	.header__menu{
		display: none;
	}

	header{
		position: fixed !important;
		top: 0;
		left: 0;
		z-index: 101;

		&:after{
			background-size: 70%, 104%;
    		background-position: -44% 12%, -552% 15%;
    		background-image: url('../images/bg_head__persons__mobile_1.png'), url('../images/bg_head__persons__mobile_2.png');
		}

		.language-box{
			display: block;

			.search-container{
			    padding-top: 27px;
    			padding-left: 34px;

				i{
					font-size: 34px;
					color: $color_yellow_black;
					background-image: url('../images/bg_games.jpg');
				    background-size: cover;
				    -webkit-background-clip: text;
				    -webkit-text-fill-color: transparent;
				    background-position: center center;
				}
			}

			.language-container{
			    padding-top: 28px;
    			padding-left: 3px;

    			ul{
    				li{
    					&:hover{
							background-image: url('../images/lang_box.png');
						}

						&.active{
							background-image: url('../images/lang_box__active.jpg');

							&:hover{
								background-image: url('../images/lang_box__active.jpg');
							}
						}

    					a{
    						padding: 11px 16px 6px;
    						font-size: 21px;
    					}
    				}
    			}
			}
		}

		.loginmenu-container{
			display: block;

			ul{
				display: block;
				float: right;
				margin-left: auto;
				margin-right: 0;

				li{
					display: block;
					float: right;
					width: auto;
				}
			}

			.login-container{
				a{
					i{
						font-size: 35px;
						margin-bottom: 2px;
					}
				}
			}

			.menu-button{
			    margin-top: 28px;
				display: block;
				width: 40px;
				height: 35px;
				position: relative;
				margin-left: 23px;

				div,
				&:before,
				&:after{
					display: block;
					width: 100%;
					left: 0;
					position: absolute;
					height: 6px;
					border-radius: 2px;
					color: $color_yellow_black;
					background-image: url('../images/bg_games.jpg');
				    background-size: cover;
					transition: 0.3s;
				}

				div{
					top: calc(50% - 3px);
				}

				&:before{
					top: 0;
					content: '';
				}

				&:after{
					bottom: 0;
					content: '';
				}

				&.active{
					div{
						left: 50%;
						width: 0px;
					}

					&:before{
						top: 14px;
					    transform: rotate(45deg);
					}

					&:after{
						bottom: 14px;
					    transform: rotate(-45deg);
					}
				}
			}
		}
	}

	.header__logo{
		position: relative;
		z-index: 1;
	    padding-top: 4px;
	    padding-bottom: 0px;
	    margin-bottom: -6px;

		.header__logo-link{
			img{
				height: 137px;
			    margin-top: -28px;
    			margin-bottom: -30px;
			}
		}
	}

	.section-head{
		display: none;

		&_scroll{
			display: none;
		}
	}

	.title-section{
	    margin-bottom: 15px;

		.title-section__text{
			font-size: 26px;
			line-height: 26px;
		}
	}

	.hot-offers{
	    margin-top: 101px;
    	margin-bottom: 61px;
	}

	.hot-offers__slider{
		.owl-dots{
			bottom: 14px;
		}

		.owl-nav{
		    bottom: 8px;

			div{
				&.owl-prev{
				    top: -2px;
					left: 14px;

					&:before{
						font-size: 60px !important;
					}
				}

				&.owl-next{
					right: 70px;

					&:before{
						font-size: 60px !important;
					}
				}
			}
		}

		.owl-item {
		    padding: 20px 0px;
		}

		.item{
			height: 254px;
		}

		.hot-offers__slider-logo{
			img{
				height: 33px;
			}
		}

		.hot-offers__slider-text{
			bottom: 9px;
    		left: 42px;
    		max-width: 300px;

    		.slider-text__title{
    			font-size: 19px;
				line-height: 19px;
    		}

    		.slider-text__text{
    			font-weight: 300;
				font-size: 16px;
				line-height: 19px;
    		}
		}
	}

	.top-sale__slider{
		padding: 0px;

		.owl-dots{
			bottom: 10px;
		}

		.owl-nav{
			bottom: 12px;

			div{
				&.owl-prev{
					left: 11px;
				}

				&.owl-next{
				    right: 80px;
    				top: 4px;
				}
			}
		}
	}

	.product__item{
		.product__item-info{
			.item-info__title{
				margin-top: 21px;
			    margin-bottom: 10px;
			    font-size: 18px;
			    line-height: 18px;
			    min-height: 40px;
			}

			.item-info__price{
			    margin-bottom: 26px;

				&_only-from{
					span{
						padding-top: 16px;
    					padding-right: 10px;
					}
				}
			}
		}

		.item-info__rating{
			margin-bottom: 14px;

			i{
				font-size: 23px;
    			margin: 0px 2px;

    			&:first-of-type{
    				margin-left: 28px;
    			}
			}

			.item-info__rating-detail{
				margin-top: -1px;
			    line-height: 31px;
			    font-size: 14px;
			    float: right;
			    margin-right: 30px;
			    margin-left: auto;
			}
		}

		.product__item-image{
			height: 261px;

			.product__item-slider-logo{
				right: 10px;

				img{
					height: 35px;
				}
			}

			.sale{
				width: 44px;
    			height: 85px;

				span{
					&:first-of-type{
						margin-top: 17px;
					    font-size: 11px;
					    line-height: 11px;
					    margin-bottom: 1px;
					}

					&:last-of-type{
						font-size: 10px;
    					line-height: 10px;
					}
				}
			}

			.label{
				span{
					font-size: 12px;
					line-height: 12px;

					&.active{
						font-size: 14px;
						line-height: 14px;
					}
				}
			}
		}
	}

	.choose-us{
	    margin-top: 65px;

	    .choose-right-top{
	    	padding-left: 0px;
	        margin-bottom: 20px;

	    	>.row{
	    		padding: 22px 0px 22px;
	    	}

	    	&:first-of-type{
		    	.choose-title{
		    		span{
			    		font-size: 13px !important;
						line-height: 13px !important;
					    margin-bottom: 1px;
					}

					p{
						font-size: 47px !important;
						line-height: 47px !important;
					    margin-bottom: -1px;
					}

					i{
			    		font-size: 18px !important;
						line-height: 18px !important;
					}
		    	}
		    }

		    &:nth-of-type(2){
		    	.choose-title{
		    		span{
			    		font-size: 18px !important;
						line-height: 18px !important;
					    margin-bottom: 1px;
					}

					p{
						font-size: 47px !important;
						line-height: 47px !important;
					    margin-bottom: -1px;
					}

					i{
			    		font-size: 13px !important;
						line-height: 13px !important;
					}
		    	}
		    }

		    &:last-of-type{
		    	.choose-title{
		    		span{
			    		font-size: 13px !important;
						line-height: 13px !important;
					    margin-bottom: 1px;
					}

					p{
						font-size: 47px !important;
						line-height: 47px !important;
					    margin-bottom: -1px;
					}

					i{
			    		font-size: 13px !important;
						line-height: 13px !important;
					}
		    	}
		    }
	    }

	    .choose-right-bottom{
	    	margin-bottom: 20px;
	    	padding: 0px;

	    	>.row{
	    	    padding: 45px 16px 14px 16px;
	    	}
	    }

	    .choose-container__inner{
	    	p{
	    		font-size: 17px;
				line-height: 20px;
	    	}

	    	a{
	    		display: inline-block;
	    		float: left;
	    		width: 46%;
	    		margin: 10px 2%;
	    		height: 35px;
	    		line-height: 35px;

	    		img{
	    			max-width: 100%;
	    			max-height: 100%;
	    			width: auto;
	    			height: auto;
	    			display: inline-block;
	    			float: none;

	    			&:last-of-type{
	    				display: none;
	    			}
	    		}
	    	}
	    }

	    .choose-left{
	    	max-width: 100%;

	    	.choose-title{
	    		margin-bottom: 0px;
	    	}

	    	>.row{
	    		padding: 20px 10px 20px;
	    	}
	    }
	}

	.publications{
		margin-top: 65px;

		.public-container{
			.public-col{
				padding: 0px;

				.public-item{
					&:nth-of-type(2){
						display: none;
					}
				}

				&:nth-of-type(2){
					display: none;
				}
			}
		}


	}

	.public-item{
		margin-bottom: 20px;

		.public-item__image{
			max-width: 232px;

			a{
				width: 232px;
    			height: 227px;
			}
		}

		.public-item__info{
			padding: 10px 20px 10px 15px;
			max-width: calc(100% - 232px);

			.public-item__info__text{
			    font-size: 14px;
    			line-height: 16px;
			}

			.public-item__info__title{
			    font-size: 18px;
    			line-height: 21px;
			}
		}
	}

	.reviews{
		margin-top: 27px;

		.title-section{
			margin-bottom: 35px;
		}
	}

	.reviews-slider{
		.product__item{
			.reviews-info__text{
				font-size: 14px;
    			line-height: 20px;

    			p{
    				height: 97px;
    			}
			}
		}
	}

	footer{
		margin-top: 65px;
		padding-top: 0px;

		>.container{
			padding-left: 0px;
			max-width: 100%;
		}

		.footer-copyright,
		.footer-information,
		.footer-contact,
		.footer-payment,
		.footer-folow{
		    max-width: 100%;
		    border-bottom: solid 1px $color_yellow;
		    padding-left: 20px;
		    padding-right: 20px;
		}

		.footer-copyright{
			border-top: solid 1px $color_yellow;
		}

		.footer-folow,
		.footer-contact{
			.footer-title{
				margin-bottom: 10px;
			}
		}

		.footer-logo,
		.footer-info{
			display: none;
		}

		.footer-info-copy{
			padding-top: 0px;
		}

		.footer-payment{
			margin-top: 30px;
			border-bottom: solid 0px $color_yellow;

			.footer-title{
				display: none;
				
			}

			.footer-info{
				display: block;

				ul{
					margin: 0 auto;
					width: 100%;
					text-align: center;

					li{
						width: auto;
						display: inline-block;
						margin: 0 10px;
						float: none;
					}
				}
			}
		}

		.active{
			.footer-title{
				&:before{
					background-image: url('../images/poly_footer__close.png');
					width: 15px;
					height: 15px;
					top: 2px;
					right: 2px;
				}
			}

			.footer-info{
				display: block;
				padding-left: 20px;
    			padding-bottom: 10px;
    			padding-top: 10px;
			}
		}

		.footer-title{
			margin-bottom: 10px;
			margin-top: 10px;
			position: relative;

			&:before{
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				background-image: url('../images/poly_footer.png');
				background-size: contain;
				background-position: center;
				width: 20px;
				height: 20px;
				display: block;z-index: 1;
				background-repeat: no-repeat;
			}

			p{
				font-size: 20px;
				line-height: 20px;
			}
		}
	}

	.mobile-menu{
		display: block;
		transform: translateY(-500px);
		transition: 0.3s;
		width: 100%;
		position: fixed;
		left: 0;
		top: 93px;
		z-index: 100;
		min-height: 200px;
		background-image: url('../images/mobile-menu.jpg');
		background-position: center;
		background-repeat: repeat;
		background-size: cover;
		box-shadow: 0px 0px 10px rgba(250, 198, 68, 0.1);

		&.active{
			transform: translateY(0);
		}

		> .row > ul{
			display: block;
			width: 100%;
			float: left;

			> li{
				display: block;
				width: 100%;
				float: left;

				&:last-of-type{
					> a{
						border-bottom: 0px;
					}
				}

				> ul{
					display: none;

					> li{
						> a{
							display: block;
								float: left;
								width: 100%;
								font-size: 18px;
								font-family: $font_ok;
								line-height: 18px;
								text-transform: uppercase;
								padding: 13px 13% 13px 20px;
								border-bottom: solid 1px rgba($color_yellow, 0.2);
							padding-left: 40px;
							color: $color_yellow_black;

							&.detail{
								position: relative;

								&:before{
									content: '';
								    position: absolute;
								    top: calc(50% - 10px);
		    						right: 18px;
								    background-image: url('../images/poly_footer.png');
								    background-size: contain;
								    background-position: center;
								    width: 20px;
								    height: 20px;
								    display: block;
								    z-index: 1;
								    background-repeat: no-repeat;
								}
							}
						}

						&.active{
								.detail{
									color: $color_yellow_hover !important;

									&:before{
										transform: rotate(90deg);
									}
								}

								> ul{
									display: block;
								}
							}

						> ul{
							display: none;

							> li{
								> a{
									float: left;
										width: 100%;
										font-size: 18px;
										font-family: $font_ok;
										line-height: 18px;
										text-transform: uppercase;
										padding: 13px 20px;
										border-bottom: solid 1px rgba($color_yellow, 0.2);
									padding-left: 60px;
									color: $color_yellow;

									
								}

								&.active{
										.detail{
											color: $color_yellow_hover !important;

											&:before{
												transform: rotate(90deg);
											}
										}

										> ul{
											display: block;
										}
									}
							}
						}
					}
				}

				&.active{
					>a{
						color: $color_yellow_hover;

						&:before{
							transform: rotate(90deg);
						}
					}

					> ul{
						display: block;
					}
				}

				> a{
					display: block;
					float: left;
					width: 100%;
					font-size: 18px;
					font-family: $font_ok;
					line-height: 18px;
					color: $color_yellow_black;
					text-transform: uppercase;
					padding: 13px 20px;
					border-bottom: solid 1px rgba($color_yellow, 0.2);

					&.detail{
						position: relative;

						&:before{
							content: '';
						    position: absolute;
						    top: calc(50% - 10px);
    						right: 18px;
						    background-image: url('../images/poly_footer.png');
						    background-size: contain;
						    background-position: center;
						    width: 20px;
						    height: 20px;
						    display: block;
						    z-index: 1;
						    background-repeat: no-repeat;
						}
					}
				}
			}
		}
	}

	.section_basket{
		width: 100%;
		height: 100%;
		top: 0;
		transform: translateX(-100%);

		.basket-list{
			max-height: 50vh;
		}

		&:after{
		    top: 380px;
		    bottom: auto;
		}
	}

	.close-basket{
		position: absolute;
		top: 5px;
		right: 9px;
		display: block;
		width: 30px;
		height: 30px;
		z-index: 10;
		transition: 0.3s;
		opacity: 1;

		&:hover{
			&:before,
			&:after{
				background-color: rgba($color_yellow_black, 1);
			}
		}

		&:before{
			position: absolute;
			content: '';
			display: block;
			width: 100%;
			height: 2px;
			top: 14px;
			left: 0;
			border-radius: 2px;
			background-color: rgba($color_yellow, 1);
			transform: rotate(45deg);
			transition: 0.3s;
		}

		&:after{
			position: absolute;
			content: '';
			display: block;
			width: 100%;
			height: 2px;
			bottom: 14px;
			left: 0;
			border-radius: 2px;
			background-color: rgba($color_yellow, 1);
			transform: rotate(-45deg);
			transition: 0.3s;
		}
	}

	.basket-button{
		.basket-button__basket{
			padding: 7px 0;
		    width: 30%;
		    text-align: center;

		    i{
		    	float: none;
		    	font-size: 30px;
		    }
		}

		.basket-button__checkout{
			padding: 9px 0 4px;
